              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Desktop Kondisi Tidak bisa tambah user -->
            <v-btn
              class="text-capitalize mr-n2 rounded-lg"
              v-show="mobile == false && validcreate == false"
              v-bind="attrs"
              v-on="on"
              color="success"
              depressed
              :disabled="validcreate == false"
            >
              <v-icon left>mdi-plus-thick</v-icon>
              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Mobile -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && validcreate == true"
              v-bind="attrs"
              v-on="on"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="loading == true"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
            
            <!-- Btn Tambah Mode Mobile Kondisi Tidak bisa tambah user -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && validcreate == false"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="validcreate == false"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
          </template>
          <v-card outlined class="rounded-lg">
            <v-toolbar flat>
              <v-toolbar-title>
                <span>{{ formTitleUser }}</span>
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn
                icon
                @click="KeluarDialogUser"
              >
                <v-icon>mdi-window-close</v-icon>
              </v-btn>
            </v-toolbar>

            <v-divider></v-divider>
            
            <v-card-text :class="$vuetify.theme.dark ? '' : ''">
              <div class="mr-n3 ml-n3 mb-n2 mt-3">
                <v-row dense v-show="editedIndex == 1" class="mb-2">
                  <v-col cols="6">
                    <v-sheet class="rounded-lg" :outlined="$vuetify.theme.dark ? true : false">
                      <v-btn depressed :color="$vuetify.theme.dark ? 'white black--text' : 'grey darken-3 white--text'" class="text-capitalize rounded-lg subtitle-1" block :loading="loading_token_mobile" @click="ResetToken('Mobile')">
                        Reset Mobile
                        <!-- <v-icon right>mdi-cellphone</v-icon> -->
                      </v-btn>
                    </v-sheet>
                  </v-col>
                  <v-col cols="6">
                    <v-sheet class="rounded-lg" :outlined="$vuetify.theme.dark ? true : false">
                      <v-btn depressed :color="$vuetify.theme.dark ? 'white black--text' : 'grey darken-3 white--text'" class="text-capitalize rounded-lg subtitle-1" block :loading="loading_token_desktop" @click="ResetToken('Desktop')">
                        Reset Desktop
                        <!-- <v-icon right>mdi-monitor</v-icon> -->
                      </v-btn>
                    </v-sheet>
                  </v-col>
                </v-row>

                <v-sheet outlined class="rounded-lg pa-2">
                  <div class="pa-2">
                    <v-row no-gutters>
                      <v-col cols="12" sm="5" md="5" lg="5">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kode</span>
                      </v-col>
                      <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1">
                        <v-text-field
                          dense
                          maxlength="15"
                          color="dark"
                          disabled
                          v-model="editedItem.Kode"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="5" md="5" lg="5">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Nama</span>
                      </v-col>
                      <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1">
                        <v-text-field
                          dense
                          counter
                          color="dark"
                          maxlength="10"
                          :hint="alertNama"
                          @keyup="cariKode(Nama)"
                          v-model="Nama"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters v-show="editedIndex == -1">
                      <v-col cols="12" sm="5" md="5" lg="5">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Password</span>
                      </v-col>
                      <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1">
                        <v-text-field
                          dense
                          :append-icon="TampilkanPassword ? 'mdi-eye' : 'mdi-eye-off'"
                          @click:append="TampilkanPassword = !TampilkanPassword"
                          :type="TampilkanPassword ? 'text' : 'password'"
                          color="dark"
                          v-model="password"
                          :disabled="textAktif"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters v-show="editedIndex == 1">
                      <v-col cols="12" sm="5" md="5" lg="5">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kata Sandi Lama</span>
                      </v-col>
                      <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          type="password"
                          disabled
                          v-model="editedItem.password"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters v-show="editedIndex == 1">
                      <v-col cols="12" sm="5" md="5" lg="5">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kata Sandi Baru</span>
                      </v-col>
                      <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="passbaru"
                          :append-icon="TampilkanPassword2 ? 'mdi-eye' : 'mdi-eye-off'"
                          @click:append="TampilkanPassword2 = !TampilkanPassword2"
                          :type="TampilkanPassword2 ? 'text' : 'password'"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters v-show="editedIndex == 1">
                      <v-col cols="12" sm="5" md="5" lg="5">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Ulangi Kata Sandi Baru</span>
                      </v-col>
                      <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          :append-icon="TampilkanPassword1 ? 'mdi-eye' : 'mdi-eye-off'"
                          :type="TampilkanPassword1 ? 'text' : 'password'"
                          v-model="verifpassbaru"
                          @click:append="TampilkanPassword1 = !TampilkanPassword1"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="5" md="5" lg="5">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kode Karyawan</span>
                      </v-col>
                      <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1">
                        <v-text-field
                          dense
                          readonly
                          color="dark"
                          :disabled="textAktif"
                          v-model="editedItem.KodeKaryawan"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                          <template v-slot:append-outer>
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  :color="$vuetify.theme.dark ? '' : 'black'"
                                  @click="DialogKodeKaryawan = true"
                                  v-show="textAktif == false && editedItem.KodeKaryawan == null || editedIndex == -1"
                                  v-bind="attrs"
                                  v-on="on"
                                >
                                  mdi-dots-horizontal
                                </v-icon>
                              </template>
                              <span>Pilih Kode Karyawan</span>
                            </v-tooltip>
                            <!-- kalo ada kode nya jadi hapus dlu  -->
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  @click="hapusKodeKaryawan"
                                  color="error"
                                  v-show="textAktif == false && editedIndex != -1 && editedItem.KodeKaryawan != null"
                                  v-bind="attrs"
                                  v-on="on"
                                >
                                  mdi-trash-can
                                </v-icon>
                              </template>
                              <span>Hapus Kode Karyawan</span>
                            </v-tooltip>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <!-- <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          dense
                          label="Kode"
                          maxlength="15"
                          color="dark"
                          disabled
                          v-model="editedItem.Kode"
                        ></v-text-field>
                      </v-col>

                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          dense
                          counter
                          label="Nama"
                          color="dark"
                          maxlength="10"
                          :hint="alertNama"
                          @keyup="cariKode(Nama)"
                          v-model="Nama"
                        ></v-text-field>
                      </v-col>

                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          dense
                          label="Password"
                          :append-icon="TampilkanPassword ? 'mdi-eye' : 'mdi-eye-off'"
                          @click:append="TampilkanPassword = !TampilkanPassword"
                          :type="TampilkanPassword ? 'text' : 'password'"
                          color="dark"
                          v-model="password"
                          v-show="editedIndex == -1"
                          :disabled="textAktif"
                        ></v-text-field>

                        <v-text-field
                          dense
                          label="Password Lama"
                          color="dark"
                          type="password"
                          disabled
                          v-model="editedItem.password"
                          v-show="editedIndex == 1"
                        ></v-text-field>
                      </v-col>

                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          dense
                          label="Password Baru"
                          color="dark"
                          v-model="passbaru"
                          :append-icon="TampilkanPassword2 ? 'mdi-eye' : 'mdi-eye-off'"
                          @click:append="TampilkanPassword2 = !TampilkanPassword2"
                          :type="TampilkanPassword2 ? 'text' : 'password'"
                          v-show="editedIndex == 1"
                        ></v-text-field>
                      </v-col>

                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          dense
                          label="Ulangi Password Baru"
                          color="dark"
                          :append-icon="TampilkanPassword1 ? 'mdi-eye' : 'mdi-eye-off'"
                          :type="TampilkanPassword1 ? 'text' : 'password'"
                          v-model="verifpassbaru"
                          @click:append="TampilkanPassword1 = !TampilkanPassword1"
                          v-show="editedIndex == 1"
                        ></v-text-field>
                      </v-col>

                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          dense
                          readonly
                          label="Kode Karyawan"
                          color="dark"
                          :disabled="textAktif"
                          v-model="editedItem.KodeKaryawan"
                        >
                          <template v-slot:append-outer>
                            
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row> -->
                  </div>
                </v-sheet>
              </div>
            </v-card-text>

            <v-divider></v-divider>
            
            <v-card-actions>
              <span class="d-inline-block text-truncate">
                Status : <span>{{Status}}</span>
              </span>
              <v-spacer></v-spacer>
              <div class="mr-n2">
                <v-btn
                  dark
                  depressed
                  :color="$vuetify.theme.dark ? 'white black--text' : 'grey darken-3'"
                  class="text-capitalize rounded-lg subtitle-1"
                  @click="Simpan"
                >
                  <span>Simpan</span>
                </v-btn>
              </div>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar> 

      <v-card outlined class="pa-2 rounded-lg" :color="$vuetify.theme.dark ? '' : 'grey lighten-2'">
        <div>
          <ejs-grid
            ref='griduser'
            style="height: 100%;"
            :height="mobile ? '43vh' : '41vh'"
            gridLines='Both'
            id="userview"
            :dataSource="DataUser"
            :allowPaging="true"
            :allowSorting='true'
            :allowResizing='true'
            :allowGrouping='true'
            :toolbar='toolbarOptions'
            :commandClick="commandClick"
            :pageSettings='pageSettings'
            :allowExcelExport='true'
            :allowPdfExport='true'
            :dataStateChange='dataStateChange'
          >
            <e-columns>
              <e-column headerText="Aksi" textAlign='Center' width="120" :commands="commands" :lockColumn='true' :customAttributes="customAttributes">
                <div class="btn-group">
                  <v-btn type="button" class="btn btn-default" (click)="prediemRowEdit($event)">
                    <i class="fa fa-pencil"></i>
                  </v-btn>
                  <v-btn type="button" class="btn btn-default" (click)="prediemRowDelete($event)">
                    <i class="fa fa-trash"></i>
                  </v-btn>
                </div>
              </e-column>
              <e-column field='Kode' headerText='Kode' textAlign='Left' width=200></e-column>
              <e-column field='Nama' headerText='Nama' width=200></e-column>
              <e-column field='karyawan.Nama' headerText='Pengguna Akun' width=200></e-column>
              <e-column field='password' headerText='Password' width=150 :visible='false'></e-column>
              <e-column field='DiBuatTgl' headerText='DiBuatTgl' textAlign='Left' width=150></e-column>
              <e-column field='DiUbahTgl' headerText='DiUbahTgl' textAlign='Left' width=150></e-column>
            </e-columns>
          </ejs-grid>
        </div>
      </v-card>

      <!-- Dialog kode karyawan -->
      <v-dialog
        v-model="DialogKodeKaryawan"
        max-width="1000px"
        scrollable
        persistent
      >
        <v-card>
          <v-toolbar
            flat
          >
            <v-toolbar-title>
              <span class="headline">Pilih Kode Karyawan</span>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn
              fab
              text
              small
              @click="DialogKodeKaryawan = false"
            >
              <v-icon>mdi-window-close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-divider></v-divider>
          <v-card-text>
            <div class="mb-n2 mr-n3 ml-n3">                                           
              <v-card outlined class="rounded-lg mt-3 elevation-6">
                <div class="pa-2">
                  <ejs-grid
                    ref='grikodejabatan'
                    height="208"
                    gridLines='Both'
                    id="kodejabatanview"
                    :dataSource="DataKodeKaryawan"
                    :allowPaging="true"
                    :allowSorting='true'
                    :allowResizing='true'
                    :toolbar='toolbarOptions'
                    :recordDoubleClick="rowDblClickKodeKaryawan"
                    :pageSettings='pageSettings'
                    :rowSelected="rowSelectedKodeKaryawan" 
                  >
                    <e-columns>
                      <e-column field='Nama' headerText='Nama' textAlign='Left' width=150></e-column>
                      <e-column field='NRK' headerText='Nrk' width=300></e-column>
                      <e-column field='TglLahir' headerText='Tanggal Lahir' width=150></e-column>
                      <e-column field='AlamatKTP' headerText='Alamat Ktp' width=200></e-column>
                      <e-column field='TempatLahir' headerText='Tempat Lahir' width=200></e-column>
                    </e-columns>
                  </ejs-grid>
                </div>
              </v-card>                                      
            </div>     
          </v-card-text>                                   
          <v-divider></v-divider>
          <v-card-actions>
            <span class="d-inline-block text-truncate">
              Status : <span>Pilih Kode</span>
            </span>
          <v-spacer></v-spacer>
            <v-btn
              dark
              color="red darken-4 mx-n2"
              class="text-capitalize"
              @click="PilihKaryawan"
            >
              <v-icon class="mr-1">mdi-check-circle</v-icon>
              Pilih
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </div>
</template>
<script>
import Vue from "vue";
import { GridPlugin, Page, Sort, Group, Resize, Toolbar, Search,CommandColumn,ExcelExport,PdfExport } from "@syncfusion/ej2-vue-grids";
import api from "@/services/http";

Vue.use(GridPlugin);

export default {
  data() {
    return {
      alert_data_disimpan: false,
      timeout_alert_simpan: 2000,
      timeout_alert_token: 1500,
      loading_token_mobile: false,
      loading_token_desktop: false,
      alert_reset_token_mobile: false,
      alert_reset_token_desktop: false,
      mobile:null,
      valid:null,
      validcreate:false,
      alertNama:'',
      statechange:null,
      UserMenuItems:[],
      RowSelectKaryawan:[],
      passbaru:null,
      textAktif:true,
      DialogKodeKaryawan:false,
      umenu:[],
      DataKodeKaryawan:[],
      verifpassbaru:null,
      loading:true,
      TampilkanPassword:false,
      TampilkanPassword2:false,
      TampilkanPassword1:false,
      Nama:null,
      DialogPilihExport:false,
      windowSize: {x: 0, y: 0},
      MenuExport: false,
      commands: [],
      customAttributes : {class: 'customcss'},
      Alert:false,
      akun:[],
      usermenu:null,
      AlertDataTerhapus:false,
      AlertBerhasilTerubah:false,
      user:[],
      token:null,
      password:'',
      DialogTambahUser: false,
      form: new FormData,
      editedIndex: -1,

      defaultItem: {
        id:"",
        Kode: "",
        Nama: "",
        password: "",
        UserMenu: "",
        KodeKaryawan:"",
        StatusData:"",
      },
      editedItem: {
        id:"",
        Kode: "",
        Nama: "",
        password: "",
        UserMenu: "",
        KodeKaryawan:"",
        StatusData:"",
      },
      toolbarOptions: ['Search'],
      DataUser: [],
      pageSettings: {pageSize: 10, pageSizes :['5','10','15','20','50','All']},
    };
  },
  

  provide: {
    grid: [Page, Sort, Group, Resize, Toolbar, CommandColumn, Search,ExcelExport,PdfExport ]
  },

  mounted(){
    this.token = localStorage.getItem('token')
    this.user = JSON.parse(localStorage.getItem('user'))
    // await Promise.all([this.saya()])
    // await this.saya()
    if (this.Permission.some(a => a == 'R')) {
      this.valid = true
      this.getDataPage(0,10)
      this.getdata()
      this.editedItem.Tgl_Masuk = this.date
      this.editedItem.Tgl_Lahir = this.date
      this.editedItem.Tgl_Phk = this.date
      // this.getKode()
    }else{
      this.loading = false
      this.valid = false
    }
    if (this.Permission.some(a => a == 'C')) {
     this.validcreate = true
    }
    if (this.Permission.some(a => a == 'U')) {
     this.commands.push({title:"Ubah", buttonOption: { cssClass: "e-flat Edit", iconCss: "e-edit e-icons", content: " "} })
    }
    if (this.Permission.some(a => a == 'D')) {
     this.commands.push({title:"Hapus", buttonOption: { cssClass:"e-flat Delete", iconCss: "e-delete e-icons", content: " "} })
    }
    // await this.getdata()
    // this.getKode()
    // console.log('oo',this.Permission)
  },

   created(){
    //get action for this page
    // this.Permission = Object.assign([],this.$route.params.Permission.replaceAll('"','').replaceAll('[','').replaceAll(']','').replaceAll(',',''))
    // var array = (new Function("return [" + this.$route.params.Permission+ "];")());
    // console.log('param',this.$route.params.Permission)
    let permisi = this.$route.params.Permission
    this.Permission = permisi == null ? [] : permisi.split(',')
    // console.log('param',this.$route.params.Permission)
    // console.log('ok',this.Permission)
    },

  computed: {
    formTitleUser() {
      return this.editedIndex === -1 ? 'Tambah User Pengguna' : 'Ubah Data Pengguna'
    },
    Status () {
      return this.editedIndex === -1 ? 'Baru' : 'Ubah'
    },
  },
  
  watch: {
  
      // let a = this.DataUser.filter( function(item){return (item.UserMenu == newval.toUpperCase());} )
      // this.umenu = a
    windowSize(){
      if (this.windowSize.x < 450) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },

    DialogTambahUser (val) {
      val || this.KeluarDialogUser()
    },
    Nama(){
      //  if (Nama != null && this.alertNama == ) {
      //     this.textAktif = false
      //   }else{
      //     this.textAktif = true
      //   }
    },

    Alert(){
      this.AlertItem()
    },
    AlertDataTerhapus(){
      this.AlertItem()
    },
    AlertBerhasilTerubah(){
      this.AlertItem()
    },
  },

  methods: {
    PilihKaryawan(){
      const user = this.RowSelectKaryawan
      api.get('user?token='+this.token+'&search='+user.KodeKaryawan+'&skip=0&take=1',{
        transformResponse: api.defaults.transformResponse.concat((data) => {
          data.result = this.$decrypt(JSON.parse(data.result))
          if(data.count == 1){
            this.editedItem.KodeKaryawan = null
            this.Toast('Karyawan ini sudah memiliki akun')
          }else{
            this.editedItem.KodeKaryawan = user.KodeKaryawan
            this.DialogKodeKaryawan = false
          }
        })
      })
      this.RowSelectKaryawan = []
    },
    ResetToken(d){
      if(d == "Mobile"){
        this.loading_token_mobile = true
        //token mobile update
        console.log('mobile')
        
        api.put("/inputToken/"+this.editedItem.id+'/?token='+this.token,{
            Tokenz:"Mobile",
            Value: null,
					},
          // { headers: {"Authorization" : `Bearer ${this.token}` } }
          ).then((res) => {
						if(res){console.log("Token Mobile Saved")}
            this.alert_reset_token_mobile = true
            this.loading_token_mobile = false
					})
      }
      if(d == "Desktop"){
        this.loading_token_desktop = true
        //token Desktop update
        console.log("Desktop")
        api.put("/inputToken/"+this.editedItem.id+'/?token='+this.token,{
            Tokenz:"Desktop",
            Value: null
					},
          // { headers: {"Authorization" : `Bearer ${this.token}` } }
          ).then((res) => {
						if(res){console.log("Token Desktop Saved")}
            this.alert_reset_token_desktop = true
            this.loading_token_desktop = false
					})
      }
    },
    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
    cariKode(Nama){
        if (Nama == null || Nama == '' || Nama.length == 0) {
          this.textAktif = true
          this.$nextTick(() => {
            this.editedItem = Object.assign({}, this.defaultItem)
          })
        }else{
      let ax = '0101/'
      let findnama = ax+Nama.toUpperCase()
        let a = this.DataUserNama.filter( function(item){return (item.Kode == findnama);} );
        if (a.length > 0) {
        this.alertNama = "Nama User ini sudah di gunakan silahkan coba yg lain"
        this.textAktif = true
        this.editedItem.Kode = ''
        }else{
          this.textAktif = false
          this.alertNama = ''
          this.editedItem.Kode = findnama
          this.editedItem.Nama = Nama
          
        }
      }
    },
    Toast(data){
      this.$swal.fire({
      toast: true,
      icon: 'success',
      title: data,
      animation: false,
      position: 'top-right',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true})
    },

    Simpan(){
      if(this.formTitleUser === "Tambah User Pengguna"){
        let data = {
          Kode: this.editedItem.Kode,
          Nama: this.editedItem.Nama,
          password: this.password,
          KodeKaryawan: this.editedItem.KodeKaryawan,
        }
        console.log(data)
        let encryptedata = this.$encrypt(data)
        // let Usermenu =  this.editedItem.UserMenu.UserMenu
        // let menu = [{"Nama":"Beranda","Object":"Link","Visible":1,"Route":"/","Icon":"mdi-home"},{"Nama":"Master","Object":"List","Visible":0,"Icon":"mdi-database"},{"Nama":"Setting","Object":"List","Visible":1,"Icon":"mdi-cog"}]
        // if (Usermenu == null || Usermenu == '') {Usermenu = this.editedItem.UserMenu}
          api.post('/register?token='+this.token,{
            data:encryptedata
            // Menu:menu
            })
            .then((res)=>{
              if (res) {
                if(this.statechange != null){
                this.dataStateChange(this.statechange)
                }else{
                  this.getDataPage(0,10)
                }
                this.getdata()
                // this.getKode()
                this.Toast('Data Di Save')
              } else {
                //
              }
            })
      }else if(this.formTitleUser === "Ubah Data Pengguna"){
        let data = {
          Kode: this.editedItem.Kode,
          Nama: this.editedItem.Nama,
          password: this.passbaru,
          StatusData: this.editedItem.StatusData,
          KodeKaryawan: this.editedItem.KodeKaryawan,
          UserMenu: this.editedItem.UserMenu
        }
        let encryptedata = this.$encrypt(data)
         api.put("/user/"+this.editedItem.id+"?token="+this.token,{
            data: encryptedata
					},
          { headers: {"Authorization" : `Bearer ${this.token}` } })
					.then((res) => {
						if (!res) {
							//do nothing
              // this.alert_data_disimpan = true
						}else{
              if(this.statechange != null){
                this.dataStateChange(this.statechange)
              }else{
                this.getDataPage(0,10)
              }
              // this.getdata()
              // this.getKode()
              this.Toast('Data Di Save')
              // this.alert_data_disimpan = true
						}
						
					})
      }
      this.KeluarDialogUser()    
    },

    commandClick: function(args) {
      if (args.target.classList.contains("custombutton")){
        // let data = JSON.stringify(args.rowData);
		
        // console.log(args.rowData);
      } else if (args.target.classList.contains("Delete")){
        var r = confirm("Yakin Hapus Data?");
        if (r == true) { 
          api
            .delete("/user/"+args.rowData.id+'?token='+this.token)
            .then((res) =>{
              // console.log(res);
              if (res) {
              if(this.statechange != null){
                this.dataStateChange(this.statechange)
              }else{
                this.getDataPage(0,10)
              }
              // this.getdata()
              this.getdata()
              this.Toast('Data Di Hapus')
              // this.AlertDataTerhapus = true
              }
              
            })
            .catch((err) => {
              console.log(err);
            });
        }
      } else if (args.target.classList.contains("Edit")){
        this.textAktif = false
        let data = args
            this.editedIndex = 1;
            // console.log(data)
            this.editedItem = data.rowData
            this.DialogTambahUser = true

          if (args.rowData.Aktif == 0) {
            this.dataTemp = args.rowData
            this.dialogAktifData = true
            }else{
                let data = args;
                this.editedIndex = 1;
                this.Kode = data.rowData.Kode
                this.editedItem.Nama = data.rowData.Nama
                this.Nama = data.rowData.Nama
                this.editedItem.Password = data.rowData.Password
                this.editedItem.StatusData = data.rowData.StatusData
                this.editedItem.UserMenu = data.rowData.UserMenu
                // this.dialogWO = true;
            }
      }
    },

    PdfExport(){
      let data = document.getElementById('userview').ej2_instances[0];
      // console.log(data)
      data.pdfExportProperties = {
        pageOrientation: 'Landscape',
        fileName:"ReportUser.pdf"
      }
      data.columns[1].visible = false
      data.columns[4].visible = false
      data.columns[3].visible = false
      // let pdfExportProperties = {
       
      // };
      // this.$refs.griduser.columns[0].visible = false;
      // this.$refs.griduser.columns[1].visible = true;
      this.$refs.griduser.pdfExport(data.pdfExportProperties);
    },
    excelExport(){
      this.$refs.griduser.excelExport();
      // console.log(this.$refs.griduser)
      // let data = document.getElementById('userview').ej2_instances[0];
      // console.log(data)
      // data.excelExport();
    },

    KeluarDialogUser () {
      this.DialogTambahUser = false
      this.$nextTick(() => {
        this.Nama = null
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.Kode = null
        this.textAktif = true
      })
    },

    // beforeExport: function(args) {
      
    // },
    rowSelectedKodeKaryawan:function(args){
      this.RowSelectKaryawan = args.data
    },
    rowDblClickKodeKaryawan: function(args){
      api.get('user?token='+this.token+'&search='+args.rowData.KodeKaryawan+'&skip=0&take=1',{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  this.loading = false
                  data.result = this.$decrypt(JSON.parse(data.result))
                  if(data.count == 1){
                    this.editedItem.KodeKaryawan = null
                    this.Toast('Karyawan ini sudah memiliki akun')
                  }else{
                    this.editedItem.KodeKaryawan = args.rowData.KodeKaryawan
                    this.DialogKodeKaryawan = false
                  }
                })
      })
    },

    dataStateChange: function(state) { 
      this.statechange = state
        if (state.action.requestType === 'paging') {
          this.skip = state.skip
          this.take = state.take
          if (state.search != undefined) {
            api.get('user?token='+this.token+'&search='+state.search[0].key+'&skip='+state.skip+'&take='+state.take,{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  this.loading = false
                  data.result = this.$decrypt(JSON.parse(data.result))
                  this.DataUser = data
                })
              },
              err => {
                console.log(err);
              }
            ) 
          } else if (state.search == undefined) {
            this.getDataPage(state.skip,state.take,this.sorted != null ? this.sorted : undefined,this.filter != null ? this.filter : undefined)
          }
        }
        if (state.action.requestType === 'sorting' || state.action.requestType === "grouping" || state.action.requestType === "ungrouping") {
          this.$refs.griduser.clearFiltering()
          this.sorted = state.sorted == undefined ? undefined : state.sorted.length > 1 ? state.sorted.map(element => {
            return element.name+ ' ' + element.direction
          }) : state.sorted[0].name+' '+state.sorted[0].direction
          if (state.sorted != undefined && state.sorted.length > 1) {
            this.sorted = new URLSearchParams({st:this.sorted}).toString()
          }
          this.getDataPage(state.skip,state.take,this.sorted)
          this.query = null
          this.filter = null
        }
        if (state.action.requestType === 'filterchoicerequest') {
          var objDistinct = {}
          let queryString = state.action.query.distincts.length > 1 ? state.action.query.distincts.at(-1) + ';' + new URLSearchParams(objDistinct).toString() : state.action.query.distincts[0]
          if (this.query != null) {
            queryString = queryString+';'+this.query
          }
          api.get('user?token='+this.token+'&dist='+queryString,{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  let res = this.$decrypt(JSON.parse(data.result))
                  state.dataSource(res)
                  localStorage.setItem('ds',JSON.stringify(res))
                })
              },
              err => {
                console.log(err);
              }
            ) 
        }
        if (state.action.requestType === "filtersearchbegin") {
          if (state.action.value == undefined) {
            state.dataSource(JSON.parse(localStorage.getItem('ds')))
          } else {
            state.dataSource(this.filterByValue(JSON.parse(localStorage.getItem('ds')),state.action.value))
          }
        }
        if (state.action.requestType === "filtering" && state.action.action !== "clear-filter") {
          let filter = {}
          let val = []
          let prop = state.where[0].predicates[0].predicates == undefined ? state.where[0].predicates[0].field : state.where[0].predicates[0].predicates[0].field
          if (state.where[0].predicates[0].predicates == undefined) {
            val.push(state.where[0].predicates[0].value)
          } else {
            state.where[0].predicates[0].predicates.forEach(element => {
              val.push(element.value)
            });
          }
          filter[prop] = val
          let url = new URLSearchParams(filter).toString()
          this.query = this.query != null ? this.query + ';' + url : url
          if (this.filter != null && this.filter.includes(prop)) {
            let jadiArr = this.filter.split(';')
            jadiArr.forEach(function(element,index,array) {
              if (element.includes(prop)) {
                array[index] = url
              }
            });
            this.filter = jadiArr.toString().replaceAll(',',';')
          } else {
            this.filter = this.filter != null ? this.filter + ';' + url : url
          }
          this.getDataPage(state.skip,state.take,undefined,this.filter)
          this.$refs.griduser.clearFiltering();
        }
        if (state.action.action === "clear-filter") {
          this.getDataPage(state.skip,state.take,this.sorted != null ? this.sorted : undefined)
          this.query = null
          this.filter = null
        }
        if (state.action.requestType === "searching") {
          state.skip = 0
          state.take = 10
          if (state.action.searchString == "" && this.$refs.griduser.$el.ej2_instances[0].pageSettings.currentPage == 1) {
            this.getDataPage(0,10)
          } else if (this.$refs.griduser.$el.ej2_instances[0].pageSettings.currentPage == 1) {
            api.get('user?token='+this.token+'&search='+state.action.searchString+'&skip='+0+'&take='+100,{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  data.result = this.$decrypt(JSON.parse(data.result))
                  this.DataUser = data
                })
              },
              err => {
                console.log(err);
              }
            ) 
          } else {
            this.$refs.griduser.$el.ej2_instances[0].pageSettings.currentPage = 1
          }
        }
      },

    getDataPage(skip,take,sort,filter){
      this.loading = true
      let cache = this.DataUser
        api.get('user?token='+this.token+'&skip='+skip+'&take='+take+'&sort='+sort+'&filter='+filter,{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  if(data.exception){
                    this.loading = false
                    this.Toast('Tidak Dapat Meminta permintaan anda')
                    this.$refs.griduser.clearSorting()
                    this.DataUser = cache
                  }
                  let datauser = {}
                  datauser.count= data.count
                  datauser.result = this.$decrypt(JSON.parse(data.result))
                  this.DataUser = datauser
                  this.loading = false
                })
              },
              err => {
                console.log(err);
              }
            ) 
            cache = this.DataUser
      },

    getdata(){
      // if (this.valid == true) {
       api.get('/getdatauser?token='+this.token,{
          transformResponse: api.defaults.transformResponse.concat((data) => {
            this.DataUserNama = this.$decrypt(data)
            this.getKode()
          })
        },
				err => {
					console.log(err);
				}
			)   
      // }
      
    },
    hapusKodeKaryawan(){
      this.editedItem.KodeKaryawan = null
    },
   getKode(){
      if (this.valid == true) {
      //  let datauser = this.DataUser
      //  api.get('/karyawankerja?token='+this.token,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
			// 	res => {
      //     let d= []
      //     datauser.forEach(element => {
      //       d.push(element.KodeKaryawan)
      //     });
      //    let datauserset = [...new Set(d)]
      //    var filtered = datauserset.filter(function (el) {
      //       return el != null;
      //     });
      //     let fild = []
      //     for (let i = 0; i < res.data.length; i++) {
      //       for (let index = 0; index < filtered.length; index++) {
      //        const element = res.data.findIndex(x => x.KodeKaryawan === filtered[index])
      //        fild.push(element)
      //       }
      //     }
      //     let datakey = [...new Set(fild)]
      //     const indexSet = new Set(datakey);
      //     const datakaryawanfiltered = res.data.filter((value, i) => !indexSet.has(i));
      //     this.DataKodeKaryawan = datakaryawanfiltered
			// 	},
			// 	err => {
			// 		console.log(err);
			// 	}
			// )
       api.get('/karyawankerja?token='+this.token,{
          transformResponse: api.defaults.transformResponse.concat((data) => {
            this.DataKodeKaryawan = this.$decrypt(data)
          })
        },
				err => {
					console.log(err);
				}
			)  
      }
    },

    Reset() {
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
      })
    },
    logout(){
        api.post('/logout', this.token)
        .then(res=>{
          // console.log(res)
          if (res) {
            //do nothing
          }
        })
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        location.reload(false)
      },

    AlertItem(){
      window.setInterval(() => {
        this.Alert = false;
        this.AlertDataTerhapus = false;
        this.AlertBerhasilTerubah = false;
      }, 3000)
    },
  },
}
